.bubble {
  position: relative;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: 5px 10px 15px black;
  margin: 15px 15px 15px 15px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center; }
  .bubble img {
    position: absolute;
    height: 110%;
    left: 0px;
    top: 0px;
    transition: 400ms ease-in-out; }
  @media (min-width: 320px) {
    .bubble {
      width: 120px;
      height: 120px; } }
  @media (min-width: 768px) {
    .bubble {
      width: 200px;
      height: 200px; } }
  @media (min-width: 992px) {
    .bubble {
      width: 250px;
      height: 250px; } }

.bubble[disabled] {
  pointer-events: none;
  z-index: -1; }

.bubble-text {
  position: absolute;
  text-align: center;
  margin-left: -50px;
  color: white;
  font-size: 2em;
  opacity: 0;
  font-weight: bold;
  transition: 400ms ease-in-out;
  white-space: nowrap;
  user-select: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none; }
  .bubble-text p {
    display: block;
    line-height: 1.2;
    font-size: 1em;
    color: white;
    font-weight: normal;
    text-align: center;
    padding: 5px 0px;
    user-select: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none; }

.bubble:hover .bubble-text, .bubble.active .bubble-text {
  margin-left: 0px;
  opacity: 1; }

.bubble:hover img, .bubble.active img {
  -webkit-filter: brightness(30%);
          filter: brightness(30%);
  -webkit-transform: translate(10%, 10%) scale(1.3);
          transform: translate(10%, 10%) scale(1.3); }

.sub-bubble {
  position: absolute;
  background-color: #0a0a0a;
  margin: auto;
  border-radius: 50%;
