.demo0 {
  border-radius: 4px;
  background-color: rgb(209, 209, 202);
  position: relative;
  margin: 5px 3px 10px;
  width: 450px;
  height: 50px;
}
.demo0-block {
  position: absolute;
  width: 50px;
  height: 50px;
  border-radius: 4px;
  background-color: rgb(130, 181, 198);
}


.grid-block{
  position: relative;
  cursor: pointer;
  float: left;
  margin: 10px 10px;
  width: 50px;
  height: 50px;
  border-radius: 4px;
  background-color: rgb(130, 181, 198);
}

.menu-box {
  top: 0;
  left:0;
  width: 50px; 
  height: 50px;
  position: relative; 
  background-color: rgb(130, 181, 198); 
  cursor: pointer;
}

.box{
background-color: red;
height: 200px;
width: 200px;
}
.alert-enter {
  opacity: 0;
  transform: scale(0.5);
}
.alert-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: opacity 500ms, transform 500ms;
}

.alert-exit {
  opacity: 1;
}
.alert-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 500ms, transform 500ms;
}
