body, html {
    height: 100%;
  }

.container .row > [class^="col"]{
    padding-top: .75rem;
    padding-bottom: .75rem;
    background-color: #E5EDF5;
    border: 1px solid #C9C1D5;
    color: #5F5F5F;
}

.container .row {
    margin-bottom: 1rem;
}

.text-1 {
  position: absolute;
  top: 100px;
}

.text-2 {
  position: absolute;
  top: 200px;
  transition: 0.5s;
}

.text-3 {
  position: absolute;
  top: 300px;
  transform: translateX(-50px);
  opacity: 0;
  transition: 0.5s;
}

.text-4 {
  position: absolute;
  top: 400px;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  opacity: 0;
  transform: translateX(-50px);
}

.target{
  position: absolute;
  top: 1000px;
  background-color: pink;
  font-size: 30px;
  border: 2px solid black;
  opacity: 0;
  transform: translateX(-50px);
  transition: 500ms;
}

.show {
  opacity: 1;
  transform: translateX(0px);
}
